body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
html{height:100%}body{height:100%}

html{height:100%}body{height:100%}.navBar{display:flex;flex-direction:row;align-items:center;height:70px;width:100vw;background-color:#7229E6;position:fixed;box-shadow:0 1px 5px 0 rgba(0,0,0,0.5)}.navBar .navBar__logo{margin-left:50px;min-width:120px;display:flex;align-items:center;color:#fff;padding:20px;text-decoration:none;font-size:24px;font-weight:700;line-height:0.8}.navBar .navBar__logo .logo__top100{font-size:18px;letter-spacing:5px}.navBar .navBar__logo .logo__top100 img{width:40px;margin-right:5px}.navBar .navBar__items .items__btn{padding:30px;text-decoration:none;text-transform:uppercase;color:#fff}.navBar .navBar__items .items__btn:hover{cursor:pointer;color:#fff}.navBar .navBar__hamburger{padding:20px;font-size:28px;color:#FFFFFF;background:transparent;border:none;display:none;cursor:pointer}@media (max-width: 690px){.navBar .navBar__hamburger{display:block}.navBar .navBar__logo{margin-left:auto}.navBar .items__btn{display:none}}@media (max-width: 690px){.navBar{justify-content:space-between}}

html{height:100%}body{height:100%}.topAlbums{display:flex;flex-direction:column;align-items:center;min-height:100vh;background:linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%),radial-gradient(at top center, rgba(255,255,255,0.4) 0%, rgba(0,0,0,0.4) 120%) #989898;background-blend-mode:multiply, multiply;color:white}.topAlbums .topAlbums__searchBox{display:flex;flex-direction:row;justify-content:center;align-items:center;height:40px;margin-top:70px;border:1px solid #aeafb0;border-radius:3px;background:linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%),radial-gradient(at top center, rgba(255,255,255,0.4) 0%, rgba(0,0,0,0.4) 120%) #989898;font-family:"Nunito",sans-serif;border:none;border:solid 1px #ccc;border-radius:10px}.topAlbums .topAlbums__searchBox .searchBox__input{font-size:20px;height:38px;background:#ffffff;border:none;border-radius:2px;padding-left:10px;border:none;border:solid 1px #ccc;border-radius:10px}.topAlbums .topAlbums__searchBox .searchBox__icon{border-radius:2px;height:20px;color:#fff;width:30px}.topAlbums .topAlbums__list{padding:0}

html{height:100%}body{height:100%}.panel{display:flex;flex-direction:column;justify-content:center;min-height:100px;background-color:linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%),radial-gradient(at top center, rgba(255,255,255,0.4) 0%, rgba(0,0,0,0.4) 120%) #989898;margin-bottom:5px;border:1px solid #aeafb0;border-radius:3px;font-family:"Nunito",sans-serif}.panel:hover{box-shadow:0 1px 5px 0 #828282}.panel:hover .header__arrow{color:#fff}.panel.is-expanded .header__arrow{transform:rotate(180deg)}.panel.is-expanded .album__artist{color:#fff}.panel .panel__header{max-width:800px;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;cursor:pointer}.panel .panel__header .header__rank{text-align:center;min-width:100px;font-size:28px;font-weight:bold}@media (max-width: 450px){.panel .panel__header .header__rank{min-width:45px}}.panel .panel__header .header__img{padding:10px 20px 10px 0;max-height:80px}@media (max-width: 450px){.panel .panel__header .header__img{max-height:50px}}.panel .panel__header .header__album .album__name{margin:0;font-size:18px;font-weight:bold}.panel .panel__header .header__album .album__artist{margin:0;font-size:16px;font-weight:normal}.panel .panel__header .header__arrow{margin-left:auto;padding:40px;font-size:20px;transition:all 0.3s ease-out}@media (max-width: 450px){.panel .panel__header .header__arrow{padding:18px}}.panel .panel__header .albumArtist{color:#1E90FF}.panel .panel__collapse{overflow:hidden;transition:height 0.3s ease-out}.panel .panel__collapse .body__info{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;height:80px;border-top:1px solid #f0f1f5;background-image:linear-gradient(to right, #6a11cb 0%, #2575fc 100%)}.panel .panel__collapse .body__info .info__element{display:flex;flex-direction:column;align-items:center}.panel .panel__collapse .body__info .info__element:last-child{padding:0;border-right:none}.panel .panel__collapse .body__info .info__element .element__data{text-align:center;font-weight:bold;font-size:18px}.panel .panel__collapse .body__info .info__element .element__title{font-size:14px;color:#fff}.panel .panel__collapse .body__links{visibility:hidden;display:flex;flex-direction:row;align-items:center;justify-content:space-around;height:80px;border-top:#f0f1f5 solid 1px}.panel .panel__collapse .body__links .links__link{text-align:center}.panel .panel__collapse .body__links .links__link:hover{color:#fff}.panel .panel__collapse .body__links .links__link .links__link--a{text-decoration:none;color:#000000;display:flex;align-items:center}.panel .panel__collapse .body__links .links__link .links__link--a img{width:20px;padding-left:5px}.panel .panel__collapse .body__links .links__link .links__link--a:hover{color:#fff}.panel .panel__collapse .body__links.is-expanded{visibility:visible}

.sideDrawer{height:100%;background:#FFFFFF;box-shadow:1px 0 7px rgba(0,0,0,0.5);position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:200;transform:translateX(-100%);transition:transform 0.4s ease-in-out}.sideDrawer.open{transform:translateX(0)}.sideDrawer .sideDrawer__items{height:100%;display:flex;flex-direction:column;justify-content:flex-start}.sideDrawer .sideDrawer__items .sideDrawer__items-btn{padding:20px;font-size:22px;text-decoration:none;color:black}.sideDrawer .sideDrawer__items .sideDrawer__items-btn:hover{background:rgba(130,130,130,0.22)}

.backdrop{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.3);z-index:100}

